	module.exports = {"pathContext":{"docHtml":"<h1>parseurl</h1>\n<p><a href=\"https://npmjs.org/package/parseurl\"><img src=\"https://img.shields.io/npm/v/parseurl.svg\" alt=\"NPM Version\"></a>\n<a href=\"https://npmjs.org/package/parseurl\"><img src=\"https://img.shields.io/npm/dm/parseurl.svg\" alt=\"NPM Downloads\"></a>\n<a href=\"https://nodejs.org/en/download/\"><img src=\"https://img.shields.io/node/v/parseurl.svg\" alt=\"Node.js Version\"></a>\n<a href=\"https://travis-ci.org/pillarjs/parseurl\"><img src=\"https://img.shields.io/travis/pillarjs/parseurl/master.svg\" alt=\"Build Status\"></a>\n<a href=\"https://coveralls.io/r/pillarjs/parseurl?branch=master\"><img src=\"https://img.shields.io/coveralls/pillarjs/parseurl/master.svg\" alt=\"Test Coverage\"></a></p>\n<p>Parse a URL with memoization.</p>\n<h2>Install</h2>\n<p>This is a <a href=\"https://nodejs.org/en/\">Node.js</a> module available through the\n<a href=\"https://www.npmjs.com/\">npm registry</a>. Installation is done using the\n<a href=\"https://docs.npmjs.com/getting-started/installing-npm-packages-locally\"><code>npm install</code> command</a>:</p>\n<pre><code class=\"language-sh\">$ npm install parseurl\n</code></pre>\n<h2>API</h2>\n<pre><code class=\"language-js\">var parseurl = require('parseurl')\n</code></pre>\n<h3>parseurl(req)</h3>\n<p>Parse the URL of the given request object (looks at the <code>req.url</code> property)\nand return the result. The result is the same as <code>url.parse</code> in Node.js core.\nCalling this function multiple times on the same <code>req</code> where <code>req.url</code> does\nnot change will return a cached parsed object, rather than parsing again.</p>\n<h3>parseurl.original(req)</h3>\n<p>Parse the original URL of the given request object and return the result.\nThis works by trying to parse <code>req.originalUrl</code> if it is a string, otherwise\nparses <code>req.url</code>. The result is the same as <code>url.parse</code> in Node.js core.\nCalling this function multiple times on the same <code>req</code> where <code>req.originalUrl</code>\ndoes not change will return a cached parsed object, rather than parsing again.</p>\n<h2>Benchmark</h2>\n<pre><code class=\"language-bash\">$ npm run-script bench\n\n> parseurl@1.3.2 bench nodejs-parseurl\n> node benchmark/index.js\n\n  http_parser@2.7.0\n  node@4.8.4\n  v8@4.5.103.47\n  uv@1.9.1\n  zlib@1.2.11\n  ares@1.10.1-DEV\n  icu@56.1\n  modules@46\n  openssl@1.0.2k\n\n> node benchmark/fullurl.js\n\n  Parsing URL \"http://localhost:8888/foo/bar?user=tj&#x26;pet=fluffy\"\n\n  3 tests completed.\n\n  fasturl   x 1,246,766 ops/sec ±0.74% (188 runs sampled)\n  nativeurl x    91,536 ops/sec ±0.54% (189 runs sampled)\n  parseurl  x    90,645 ops/sec ±0.38% (189 runs sampled)\n\n> node benchmark/pathquery.js\n\n  Parsing URL \"/foo/bar?user=tj&#x26;pet=fluffy\"\n\n  3 tests completed.\n\n  fasturl   x 2,077,650 ops/sec ±0.69% (186 runs sampled)\n  nativeurl x   638,669 ops/sec ±0.67% (189 runs sampled)\n  parseurl  x 2,431,842 ops/sec ±0.71% (189 runs sampled)\n\n> node benchmark/samerequest.js\n\n  Parsing URL \"/foo/bar?user=tj&#x26;pet=fluffy\" on same request object\n\n  3 tests completed.\n\n  fasturl   x  2,135,391 ops/sec ±0.69% (188 runs sampled)\n  nativeurl x    672,809 ops/sec ±3.83% (186 runs sampled)\n  parseurl  x 11,604,947 ops/sec ±0.70% (189 runs sampled)\n\n> node benchmark/simplepath.js\n\n  Parsing URL \"/foo/bar\"\n\n  3 tests completed.\n\n  fasturl   x 4,961,391 ops/sec ±0.97% (186 runs sampled)\n  nativeurl x   914,931 ops/sec ±0.83% (186 runs sampled)\n  parseurl  x 7,559,196 ops/sec ±0.66% (188 runs sampled)\n\n> node benchmark/slash.js\n\n  Parsing URL \"/\"\n\n  3 tests completed.\n\n  fasturl   x  4,053,379 ops/sec ±0.91% (187 runs sampled)\n  nativeurl x    963,999 ops/sec ±0.58% (189 runs sampled)\n  parseurl  x 11,516,143 ops/sec ±0.58% (188 runs sampled)\n</code></pre>\n<h2>License</h2>\n<p>  <a href=\"LICENSE\">MIT</a></p>","docPath":"argo-ci/node_modules/parseurl/readme","proj":"argo-ci"}}

